import React, { useEffect,useState, useRef } from 'react';
import './search.less'
export default function Search({list,onChange,searchName = 'bTokenSymbol'}){
  const [search,setSearch] = useState('')
  const origin = useRef([])
  const searchChange = (e)=>{
    let value = e.target.value
    setSearch(value)
  }

  useEffect(()=>{
    if(search){
      let value = search.toLocaleUpperCase()
      if(origin.current.length === 0){
        origin.current = list
      }
      const arr = origin.current.filter(el =>  el[searchName] && el[searchName].indexOf(value) >= 0)
      onChange(arr)
    } else {
      onChange(origin.current)
    }
  },[search])
  return(
    <div className='search-token'>
      <input 
      type='text'
      className='search-input'
      placeholder='search'
      value={search}
      onChange={(e)=>{searchChange(e)}}
      />
    </div>
  )
}